import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import usersService from "../services/usersService";

const AdminEdit = ({ titlePage }) => {
  const { id } = useParams();
  const [editUser, setEditUser] = useState({});
  const navigate = useNavigate()
  const handleChangeBusiness = async () => {
    await usersService.patchUserBusinessStatus(id);
    window.location.reload();
  };
  const handleDeleteUser = async ()=> {
    await usersService.deleteUser(id)
    navigate("/admin-crm")
  }
  useEffect(() => {
    if (!editUser) {
      return null;
    }
    async function editUserByAdm() {
      const res = await usersService.getUserById(id);
      setEditUser(res.data);
    }
    editUserByAdm();
  }, []);
  return (
    <div className="d-flex flex-column align-items-center">
      <h1 className="my-3">{titlePage}</h1>
      <ul className="list-unstyled">
        <li><b>User ID:</b> {editUser._id}</li>
        <li>
          <b>User Name:</b> {editUser.name?.first} {editUser.name?.middle}{" "}
          {editUser.name?.last}
        </li>
        <li><b>Email:</b> {editUser.email}</li>
        <li>
          <b>User Business:</b>{" "}
          {editUser.isBusiness ? "is Business" : "Is not Business"}
        </li>
      </ul>
      {editUser.isBusiness ? (
        <button onClick={handleChangeBusiness} className="btn btn-info mb-3">
          Change To Not Business
        </button>
      ) : (
        <button onClick={handleChangeBusiness} className="btn btn-info mb-3">
          Change To Business
        </button>
      )}
          <button onClick={handleDeleteUser} className="btn btn-danger mb-3">Delete User</button>
    </div>
  );
};

export default AdminEdit;
