import { NavLink } from "react-router-dom";
import { useAuth } from "../context/user.context";

const NavbarBottom = () => {
  const { user } = useAuth();
  return (
    <nav className="navbar fixed-bottom navbar-expand bg-body-tertiary ">
      <div className="container-fluid d-flex justify-content-center">
        <ul className="navbar-nav">
          <li className="nav-item text-center">
            <NavLink
              to={"/"}
              className="nav-link"
              aria-current="page"
              href="..."
            >
              <i className="bi bi-house"></i> <div>Home</div>
            </NavLink>
          </li>
          <li className="nav-item text-center">
            <NavLink
              to={"/about"}
              className="nav-link"
              aria-current="page"
              href="..."
            >
              <i className="bi bi-info-circle"></i>
              <div>About</div>
            </NavLink>
          </li>
          {user ? (
            <li className="nav-item text-center">
              <NavLink
                to={"/fav-cards"}
                className="nav-link"
                aria-current="page"
                href="..."
              >
                <i className="bi bi-heart-fill text-danger"></i>
                <div>Favorites</div>
              </NavLink>
            </li>
          ) : (
            ""
          )}
          {user && user.isBusiness ? (
            <li className="nav-item text-center">
              <NavLink
                to={"/my-cards"}
                className="nav-link"
                aria-current="page"
                href="..."
              >
                <i className="bi bi-pass-fill"></i>
                <div> My-cards</div>
              </NavLink>
            </li>
          ) : (
            ""
          )}
          {user && user.isAdmin ? (
            <li className="nav-item text-center">
              <NavLink
                to={"/admin-crm"}
                className="nav-link"
                aria-current="page"
                href="..."
              >
                <i class="bi bi-wrench"></i> <div> CRM</div>
              </NavLink>
            </li>
          ) : (
            ""
          )}
        </ul>
      </div>
    </nav>
  );
};

export default NavbarBottom;
