import { useState, useEffect } from "react";
import cardsService from "../services/cardsService";
import { useLike } from "../context/like.context";
import Card from "./card";

const HomePage = () => {
  const [cards, setCards] = useState([]);
  const { likedCards } = useLike();
  const [displayedCards, setDisplayedCards] = useState(3); 

  useEffect(() => {
    const getAllCards = async () => {
      const data = await cardsService.getAllCards();
      setCards(data);
    };
    getAllCards();
  }, [likedCards]);

  const loadMoreCards = () => {
    setDisplayedCards((prevDisplayedCards) => prevDisplayedCards + 3);
  };

  return (
    <>
      <h2 className="text-center my-3">
        Welcome to Visit<i className="bi bi-person"></i>Cards!
      </h2>
      <p className="display-6 bg-info p-4 rounded ">
        <em>
          Simplify your professional connections with our streamlined Digital
          Business Cards. Designed for efficiency and impact, our platform
          allows you to create a clean and effective online business card that
          speaks volumes about your business.
        </em>
      </p>


      <div className="my-4 text-center bg-info rounded">
        <div className="d-flex justify-content-center flex-wrap gap-3">
          {cards.slice(0, displayedCards).map((card) => (
            <Card card={card} key={card._id} />
          ))}
        </div>

        {displayedCards < cards.length && (
          <button className="btn btn-outline-dark my-3" onClick={loadMoreCards}>
            More...
          </button>
        )}
      </div>
    </>
  );
};

export default HomePage;
