import React from "react";
import { Link } from "react-router-dom";
import { useAuth } from "../context/user.context";
import { useLike } from "../context/like.context";

const Card = ({
  card: {
    title,
    subtitle,
    description,
    phone,
    email,
    web,
    image,
    address,
    _id,
  },
}) => {
  const { user } = useAuth();
  const { likedCards, addFavorite, removeFavorite } = useLike();

  const liked = likedCards.some((likedCard) => likedCard._id === _id);


  return (
    <div className="card my-3" style={{ width: "17rem" }}>
      <img src={image && image.url} className="card-img-top" alt={image && image.alt} />
      <div className="card-body">
        <h5 className="card-title">{title}</h5>
        <h6 className="card-title">{subtitle}</h6>
        <p className="card-text">{description}</p>

        <ul className="list-group list-group-flush">
          <li className="list-group-item">Email: {email}</li>
          {address?.country && <li className="list-group-item">Address: {`${address.country}, ${address.state}, ${address.city} `}</li>}
          {address?.street && <li className="list-group-item">street: {`${address.street} - ${address.houseNumber}`}</li>}
          {address?.zip && <li className="list-group-item">zip: {address.zip}</li>}
          <li className="list-group-item">phone: {phone}</li>
          {web && <li className="list-group-item">web: {web}</li>}
        </ul>

        <div className="text-center">
          {user && (
         <span
         className="mx-3"
         onClick={() => {
           if (liked) {
             console.log("Removing favorite:", _id);
             removeFavorite(_id);
           } else {
             console.log("Adding favorite:", _id);
             addFavorite(_id);
           }
         }}
       >
              {liked ? (
                <i className="bi bi-heart-fill text-danger"></i>
              ) : (
                <i className="bi bi-heart"></i>
              )}
            </span>
          )}
          {user && user.isBusiness && (
            <>
              <Link to={`/cards/edit/${_id}`} className="card-link">
                <button className="btn btn-sm btn-info">
                  <i className="bi bi-pen"></i>
                </button>
              </Link>
              <Link to={`/cards/${_id}`} className="card-link">
                <button className="btn btn-sm btn-danger">
                  <i className="bi bi-trash"></i>
                </button>
              </Link>
              <Link to={`/business-page/${_id}`} className="card-link">
                <button className="btn btn-sm btn-success">
                  <i className="bi bi-pin-map-fill"></i>
                </button>
              </Link>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default Card;