import { useState, useEffect } from "react";
import { useFormik } from "formik";
import { useNavigate } from "react-router-dom";
import { validateFormikUsingJoi } from "../utilis/validateFormikUsingJoi";
import Joi from "joi";
import { useAuth } from "../context/user.context";
import usersService from "../services/usersService";
import Input from "./commons/input";

const ProfileEdit = ({ titlePage}) => {
  const { user } = useAuth();
  const [userObj, setUserObj] = useState({});
  const [serverError, setServerError] = useState("");

  useEffect(() => {
    const fetchData = async () => {
      try {
        const userId = usersService.getUser()._id;
        const response = await usersService.getUserById(userId);
        setUserObj(response.data);
      } catch (error) {
        return null;
      }
    };
    
    fetchData();
  }, [user]);

  const navigate = useNavigate();
  const formik = useFormik({
    validateOnMount: true,
    initialValues: {
      firstName: "",
      middleName: "",
      lastName: "",
      phone: "",
      url: "",
      alt: "",
      state: "",
      country: "",
      city: "",
      street: "",
      houseNumber: "",
      zip: "",
    },
    validate: validateFormikUsingJoi({
      firstName: Joi.string().min(2).max(256).required(),
      middleName: Joi.string().min(2).max(256).allow(""),
      lastName: Joi.string().min(2).max(256).required(),
      phone: Joi.string().min(9).max(11).required(),
      url: Joi.string().min(14).allow(""),
      alt: Joi.string().min(2).max(256).allow(""),
      state: Joi.string().min(3).max(256),
      country: Joi.string().min(2).max(256).required(),
      city: Joi.string().min(2).max(256).required(),
      street: Joi.string().min(2).max(256).required(),
      houseNumber: Joi.number().min(2).max(256).required(),
      zip: Joi.string().min(2).max(256).required(),
    }),
    async onSubmit(values) {
      try {
        const {
          firstName,middleName,lastName,phone,url,alt,state,country,city,street,houseNumber,zip,
        } = values;
        const finalUserObj = {
          name: {
            first: firstName,
            middle: middleName,
            last: lastName,
          },
          phone: phone,
          image: {
            url: url,
            alt: alt,
          },
          address: {
            state: state,
            country: country,
            city: city,
            street: street,
            houseNumber: houseNumber,
            zip: zip,
          },
        };
        await usersService.updateUser(user._id, finalUserObj);
      } catch (error) {
        setServerError(error.response.data || "An error occurred");
      }
      navigate("/");

    },
  });

  return (
    <>
      <h1 className="text-center mt-3">{titlePage}</h1>
    <div className="container flex-column d-flex justify-content-center ">
      {serverError && (
        <div className="alert alert-danger text-center"> {serverError}</div>
      )}
      <form onSubmit={formik.handleSubmit} className=" row g-3 mt-5">
        <div className="col-md-4">
          <Input
            {...formik.getFieldProps("firstName")}
            title={"First Name"}
            error={formik.touched.firstName && formik.errors.firstName}
            placeholder = {userObj.name && userObj.name.first}
            required
          />
        </div>
        <div className="col-md-4">
          <Input
            {...formik.getFieldProps("middleName")}
            title={"Middle Name"}
            error={formik.touched.middleName && formik.errors.middleName}
            placeholder = {userObj.name && userObj.name.middle}
          />
        </div>
        <div className="col-md-4">
          <Input
            {...formik.getFieldProps("lastName")}
            title={"Last Name"}
            error={formik.touched.lastName && formik.errors.lastName}
            placeholder = {userObj.name && userObj.name.last}
            required
          />
        </div>
        <div className="col-md-12">
          <Input
            {...formik.getFieldProps("phone")}
            title={"Phone"}
            error={formik.touched.phone && formik.errors.phone}
            placeholder = {userObj.phone}
            required
          />
        </div>
       
        <div className="col-md-6">
          <Input
            {...formik.getFieldProps("url")}
            title={"Image URL"}
            error={formik.touched.url && formik.errors.url}
            placeholder = {userObj.image && userObj.image.url}
          />
        </div>
        <div className="col-md-6">
          <Input
            {...formik.getFieldProps("alt")}
            title={"Alt"}
            error={formik.touched.alt && formik.errors.alt}
            placeholder = {userObj.image && userObj.image.alt}

          />
        </div>
        <div className="col-md-6">
          <Input
            {...formik.getFieldProps("country")}
            title={"Country"}
            error={formik.touched.country && formik.errors.country}
            placeholder = {userObj.address && userObj.address.country}
            required
          />
        </div>
        <div className="col-md-6">
          <Input
            {...formik.getFieldProps("state")}
            title={"State"}
            error={formik.touched.state && formik.errors.state}
            placeholder = {userObj.address && userObj.address.state}
            required
          />
        </div>{" "}
        <div className="col-md-6">
          <Input
            {...formik.getFieldProps("city")}
            title={"City"}
            error={formik.touched.city && formik.errors.city}
            placeholder = {userObj.address && userObj.address.city}
            required
          />
        </div>
        <div className="col-md-6">
          <Input
            {...formik.getFieldProps("street")}
            title={"Street"}
            error={formik.touched.street && formik.errors.street}
            
            placeholder = {userObj.address && userObj.address.street}
            required
          />
        </div>
        <div className="col-md-6">
          <Input
            {...formik.getFieldProps("houseNumber")}
            title={"House Number"}
            error={formik.touched.houseNumber && formik.errors.houseNumber}
            placeholder = {userObj.address && userObj.address.houseNumber}
            required
          />
        </div>
        <div className="col-md-6">
          <Input
            {...formik.getFieldProps("zip")}
            title={"Zip-code"}
            error={formik.touched.zip && formik.errors.zip}
            placeholder = {userObj.address && userObj.address.zip}
            required
          />
        </div>
        
        <button
          disabled={!formik.isValid}
          type="submit"
          className="btn btn-info my-3"
        >
          Submit
        </button>
      </form>
    </div>
    </>
  );
};

export default ProfileEdit;
