import { createContext, useContext, useState, useEffect } from "react";
import cardsService from "../services/cardsService"; // Importe o serviço adequado
import { useAuth } from "./user.context";

const likeContext = createContext();

export const LikeProvider = ({ children }) => {
  const [likedCards, setLikedCards] = useState([]);
  const { user } = useAuth();

  useEffect(() => {
    const fetchData = async () => {
      if (!user) {
        return null;
      }
      try {
        const cards = await cardsService.getAllCards();
        const userLikedCards = cards.filter((card) =>
          card.likes.includes(user?._id)
        );
        setLikedCards(userLikedCards);
      } catch (error) {
        console.error("Error fetching liked cards:", error);
      }
    };

    fetchData();
  }, [user]); 

  const addFavorite = async (cardId) => {
    try {
      // API REQUEST FOR LIKE
      await cardsService.likeCard(cardId);

      // if ok will set de like into a state with the likes before
      setLikedCards((prevLikedCards) => [...prevLikedCards, { _id: cardId }]);
    } catch (error) {
      console.error("Error liking card:", error);
    }
  };

  const removeFavorite = async (cardId) => {
    // API REQUEST FOR UNLIKE
    await cardsService.likeCard(cardId);

    try {
      // REMOVE THE LIKE FROM THE STATE
      setLikedCards((prevLikedCards) =>
        prevLikedCards.filter((card) => card._id !== cardId)
      );
    } catch (error) {
      console.error("Error unliking card:", error);
    }
  };

  const contextValue = {
    likedCards,
    addFavorite,
    removeFavorite,
  };

  return (
    <likeContext.Provider value={contextValue}>{children}</likeContext.Provider>
  );
};

export const useLike = () => {
  const context = useContext(likeContext);
  if (!context) {
    throw new Error("useLike must be used within a LikeProvider");
  }
  return context;
};
