import axios from "axios";

axios.defaults.baseURL = "https://monkfish-app-z9uza.ondigitalocean.app/bcard2"

export function setCommonHeader(headerName, headerValue){
  axios.defaults.headers.common[headerName] = headerValue
}

 const httpService = {
  get: axios.get,
  post: axios.post,
  put: axios.put,
  patch:axios.patch,
  delete: axios.delete,
  setCommonHeader
};


export default httpService
