import { useNavigate } from "react-router-dom";

const About = ({ pageTitlte, pageDescription }) => {
  const navigate = useNavigate();
  const handleSubmit = () => {
    navigate("/sign-up");
  };
  return (
    <>
      <h1 className="text-center my-3">{pageTitlte}</h1>
      <div className="container">
        <p>
          Welcome to Visit <span className="bi bi-person"></span>Cards, where
          <strong className="text-info"> creativity </strong> meets
          professionalism in the world of business cards!
        </p>
        <p>
          We believe that your
          <strong className="text-info"> business card should be a reflection of your unique identity</strong>
          , and that's why we've created a platform that empowers users to
          design personalized and memorable business cards effortlessly.
        </p>
        <h5 className="text-center">
          <strong>For Business Users:</strong>
        </h5>
        <p>
          As a business user on our platform, you have the power to take your
          business card to the next level. Stand out from the crowd by adding a
          custom internet URL as your card's image. Craft compelling titles,
          captivating subtitles, and detailed descriptions that tell{" "}
          <strong className="text-info"> your business story </strong>.
        </p>
        <p>
          Explore the creativity of{" "}
          <strong className="text-info">our community</strong> by liking and
          disliking cards for inspiration. Every card you like becomes a source
          of ideas that can elevate your own design. Plus, with the ability to
          edit and refine your cards, you have the flexibility to ensure <strong className="text-info">
            your
            business card always reflects your evolving brand
          </strong>.
        </p>
        <h5 className="text-center">
          <strong>For Regular Users:</strong>
        </h5>
        <p>
          Even if you're not a business user, our platform has something special
          for you. Discover a world of creativity by exploring and liking
          business cards created by our talented community. Every card you like
          is saved to your "Fav cards" page, making it easy to revisit and draw
          <strong className="text-info"> inspiration </strong> whenever you need it. We've made it <strong className="text-info"> simple </strong> for everyone to
          appreciate and collect unique business card designs.
        </p>
        <p>
          At Visit <span className="bi bi-person"></span>Cards, we believe that
          <strong className="text-info"> every connection starts with a memorable introduction </strong>. Join us in
          redefining the way business cards are created and shared. Let your
          creativity flow, and leave a lasting impression with business cards
          that speak volumes about you and your brand.
        </p>
        <p>
          Ready to get started? <strong className="text-info"> Sign up </strong> now and unlock a world of endless
          possibilities in business card design!
        </p>

        <button onClick={handleSubmit} className="btn btn-outline-info mb-2">
          Sign Up
        </button>
      </div>
    </>
  );
};

export default About;
