import Joi from "joi";
import { useFormik } from "formik";
import { useNavigate,Navigate } from "react-router-dom";
import { useState } from "react";

import Input from "./commons/input";
import usersService from "../services/usersService";
import { useAuth } from "../context/user.context";
import { validateFormikUsingJoi } from "../utilis/validateFormikUsingJoi";
import Alert from "./commons/alerts";



const SignUp = ({ titlePage,redirect }) => {
  const {user, login } = useAuth();
  const [serverError, setServerError] = useState("");
  const [showSuccessAlert, setShowSuccessAlert] = useState(false);
  const navigate = useNavigate();
  const formik = useFormik({
    validateOnMount: true,
    initialValues: {
      firstName: "",
      middleName:"",
      lastName: "",
      phone: "",
      email: "",
      password: "",
      url: "",
      alt: "",
      isBusiness: false,
      state: "",
      country: "",
      city: "",
      street: "",
      houseNumber: "",
      zip: "",
    },
    validate: validateFormikUsingJoi({
      firstName: Joi.string().min(2).max(256).required(),
      middleName: Joi.string().min(2).max(256).allow(""),
      lastName: Joi.string().min(2).max(256).required(),
      phone: Joi.string().min(9).max(11).required(),
      email: Joi.string()
        .min(5)
        .email({ tlds: { allow: false } })
        .required(),
      password: Joi.string().min(8).max(20).regex(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d.*\d.*\d.*\d)(?=.*[!@%$#^&*-_]).{8,}$/).required(),
      url: Joi.string().min(14).allow(""),
      alt: Joi.string().min(2).max(256).allow(""),
      isBusiness: Joi.boolean(),
      state: Joi.string().min(3).max(256),
      country: Joi.string().min(2).max(256).required(),
      city: Joi.string().min(2).max(256).required(),
      street: Joi.string().min(2).max(256).required(),
      houseNumber: Joi.number().min(2).max(256).required(),
      zip: Joi.number().min(2).max(256).required(),
    }),
    async onSubmit(values) {
      try {
        const finalUserObj = {
          name: {
            first: values.firstName,
            middle:values.middleName,
            last: values.lastName,
          },
          phone: values.phone,
          email: values.email,
          password: values.password,
          image: {
            url: values.url,
            alt: values.alt,
          },
          isBusiness: values.isBusiness,
          address: {
            state: values.state,
            country: values.country,
            city: values.city,
            street: values.street,
            houseNumber: values.houseNumber,
            zip: values.zip,
          },
        };
        await usersService.register(finalUserObj);
        
        setShowSuccessAlert(true);
        setTimeout(() => {
          navigate(redirect);
        }, 2 * 1000);
        await login({ email: values.email, password: values.password });
      } catch (error) {
        setServerError(error.response.data);
      }
    },
  });
  if (user) {
    return <Navigate to={"/"} />;
  }
  return (
    <>
    <h1 className="text-center mt-3">{titlePage}</h1>
    <p className="text-center">It's <span className="text-info">free</span></p>
      {serverError && (
        <div className="alert alert-danger text-center"> {serverError}</div>
      )}
      <form onSubmit={formik.handleSubmit} className=" row g-3 mt-5">
        <div className="col-md-4">
          <Input
            {...formik.getFieldProps("firstName")}
            title={"First Name"}
            error={formik.touched.firstName && formik.errors.firstName}
            required
          />
        </div>
        <div className="col-md-4">
          <Input
            {...formik.getFieldProps("middleName")}
            title={"Middle Name"}
            error={formik.touched.middleName && formik.errors.middleName}
            
          />
        </div>
        <div className="col-md-4">
          <Input
            {...formik.getFieldProps("lastName")}
            title={"Last Name"}
            error={formik.touched.lastName && formik.errors.lastName}
            required
          />
        </div>
        <div className="col-md-4">
          <Input
            {...formik.getFieldProps("phone")}
            title={"Phone"}
            error={formik.touched.phone && formik.errors.phone}
            required
          />
        </div>
        <div className="col-md-4">
          <Input
            {...formik.getFieldProps("email")}
            title={"Email"}
            error={formik.touched.email && formik.errors.email}
            required
          />
        </div>
        <div className="col-md-4">
          <Input
            {...formik.getFieldProps("password")}
            title={"Password"}
            type={"password"}
            error={formik.touched.password && formik.errors.password}
            required
          />
        </div>
        <div className="col-md-6">
          <Input
            {...formik.getFieldProps("url")}
            title={"Image URL"}
            error={formik.touched.url && formik.errors.url}
          />
        </div>
        <div className="col-md-6">
          <Input
            {...formik.getFieldProps("alt")}
            title={"Alt"}
            error={formik.touched.alt && formik.errors.alt}
          />
        </div>
        <div className="col-md-6">
          <Input
            {...formik.getFieldProps("country")}
            title={"Country"}
            error={formik.touched.country && formik.errors.country}
            required
          />
        </div>
        <div className="col-md-6">
          <Input
            {...formik.getFieldProps("state")}
            title={"State"}
            error={formik.touched.state && formik.errors.state}
            required
          />
        </div> <div className="col-md-6">
          <Input
            {...formik.getFieldProps("city")}
            title={"City"}
            error={formik.touched.city && formik.errors.city}
            required
          />
        </div>
        <div className="col-md-6">
          <Input
            {...formik.getFieldProps("street")}
            title={"Street"}
            error={formik.touched.street && formik.errors.street}
            required
          />
        </div>
        <div className="col-md-6">
          <Input
            {...formik.getFieldProps("houseNumber")}
            title={"House Number"}
            error={formik.touched.houseNumber && formik.errors.houseNumber}
            required
          />
        </div>
        <div className="col-md-6">
          <Input
            {...formik.getFieldProps("zip")}
            title={"Zip-code"}
            error={formik.touched.zip && formik.errors.zip}
            required
          />
        </div>
        <div>
          <label className="me-2" htmlFor="biz">
            Business Account:
          </label>
          <input
            {...formik.getFieldProps("isBusiness")}
            type="checkbox"
            name="isBusiness"
            id="biz"
          />
        </div>
        <button
          disabled={!formik.isValid}
          type="submit"
          className="btn btn-info my-3"
        >
          Submit
        </button>
      <div className="my-3">
          {showSuccessAlert && (
            <Alert message="User created successfully!" type="success" />
          )}
        </div>
      </form>
    </>
  );
};
export default SignUp;



