import { createContext, useContext,useState,useEffect } from "react";


import cardsService from "../services/cardsService";
import { useNavigate } from "react-router-dom";

export const searchContext = createContext();

export function SearchContextProvider({ children }) {
    const [search, setSearch] = useState("");
    const [cards, setCards] = useState([]);
    const [cardToShow, setCardToShow] = useState([]);
    const navigate = useNavigate()
  
    useEffect(() => {
      // API REQUEST TO RECEIVE ALL CARDS WHEN PAGE UP
      async function fetchData() {
        setCards(await cardsService.getAllCards());
      }
      fetchData();
    }, []);
  // FILTERING THE CARDS TO SHOW THE RESULT AND DIRECTING FOR THE RIGHT PAGE
    const handleClick = () => {
      if (!cards) {
        return null;
      }
      setCardToShow(cards.filter((item) => item.title.includes(search)));
      navigate("/search-results")
    };
  
    const handleChange = (e) => {
      setSearch(e.target.value);
    };
    
  return (
    <searchContext.Provider
      value={{
        search,
        cards,
        cardToShow,
        handleClick,
        handleChange,
      }}
    >
      {children}
    </searchContext.Provider>
  );
}

export const useSearch = () => useContext(searchContext)
  