import React from 'react';
import { useForm, ValidationError } from '@formspree/react';
function ContactUs() {
  const [state, handleSubmit] = useForm("mdorvbpv");
  if (state.succeeded) {
      return <p className='text-center my-5'>Thanks, between 1 or 2 days and we will answer your question!</p>;
  }
  if (state.errors) {
      return <p className='text-center my-5'>I`m sorry, something going wrong here, please fill all the fields</p>;
  }
  return (
    <>
    <h1 className="text-center my-3">Contact-us</h1>
      <form className='d-flex align-items-center flex-column container gap-3 my-3 bg-info p-5 rounded' onSubmit={handleSubmit}>
      <div className='d-flex gap-2 w-75'>
        <label htmlFor="email">
          Email: 
        </label>
        <input
          className='form-control'
          id="email"
          type="email"
          name="email"
          required
        />
        <ValidationError
          prefix="Email"
          field="email"
          errors={state.errors}
        />
      </div>


      <div className="d-flex gap-2 w-75">
        <label htmlFor="message">Message:</label>
        <textarea
        className='form-control'
          id="message"
          name="message"
          required
        />
        <ValidationError
          prefix="Message"
          field="message"
          errors={state.errors}
        />
      </div>
      <button className='btn btn-outline-dark' type="submit" disabled={state.submitting}>
        Submit
      </button>
    </form>
    </>
  );
}

export default ContactUs;