import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import cardsService from "../services/cardsService";
import Card from "./card";

const MyCards = ({ titlePage }) => {
  const [cards, setCards] = useState([]);

  useEffect(() => {
    const getMyCards = async () => {
      const { data } = await cardsService.getAllMyCards();
      setCards(data);
    };

    getMyCards();
  }, []);

  return (
    <>
      <h1 className="text-center my-3">{titlePage}</h1>

      <div className="d-flex flex-wrap justify-content-center gap-2">
        {cards.length === 0 ? (
          <span>
            No cards to show, <Link to={"/createCard"}>Create New Card</Link>
          </span>
        ) : (
          cards.map((card) => <Card card={card} key={card._id} />)
        )}
      </div>

     {cards.length > 0 ? <Link to={"/createCard"} className="d-flex justify-content-center">
        <button className="btn btn-info mb-2">
          <i className="bi bi-plus"></i>
        </button>
      </Link> : ""}
    </>
  );
};

export default MyCards;
