import { useEffect, useState } from "react";
import usersService from "../services/usersService";
import { Link } from "react-router-dom";

const AdminPage = ({ titlePage }) => {
  const [allUsers, setAllUsers] = useState([]);

  useEffect(() => {
    async function admGetAllUser() {
      const res = await usersService.getAllUsers().catch(() => {});
      setAllUsers(res.data);
    }
    admGetAllUser();
  }, []);
  return (
    <>
      <h1 className="text-center my-3">{titlePage}</h1>
      <div className="d-flex flex-column align-items-center my-2">
        {allUsers.map((user) => (
          <div>
            <b> {user.name?.first} : </b>
            <Link to={`/admin-edit/${user._id}`} key={user._id}>
              {user._id}
            </Link>
          </div>
        ))}
      </div>
    </>
  );
};

export default AdminPage;
