import { useEffect, useState } from "react";
import { useLike } from "../context/like.context";
import { useAuth } from "../context/user.context";
import cardsService from "../services/cardsService";
import Card from "./card";

const FavCards = ({ titlePage }) => {
  const { user } = useAuth();
  const {  likedCards } = useLike();
  const [allCards, setAllCards] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const cards = await cardsService.getAllCards();
        setAllCards(cards);
      } catch (error) {
        console.error("Error fetching cards:", error);
      }
    };

    fetchData();
  }, [likedCards]);
  const likeddCards = allCards.filter((card) => card.likes.includes(user._id));

  return (
    <>
      <h1 className="text-center my-3">{titlePage}</h1>
      <div className="container d-flex flex-wrap justify-content-center gap-3">
        {likeddCards.length > 0 ? (
          likeddCards.map((card) => <Card card={card} key={card._id} />)
        ) : (
          <p>No favorite cards found.</p>
        )}
      </div>
    </>
  );
};

export default FavCards;