import { useState, useEffect } from "react";
import { NavLink } from "react-router-dom";

import { useAuth } from "../context/user.context";
import { useTheme } from "../context/theme.context";
import usersService from "../services/usersService";
import SearchBar from "./searchBar";

const Navbar = () => {
  const { user } = useAuth();
  const [userObj, setUserObj] = useState({});
  const { toggleTheme } = useTheme();
  const handleSubmit = () => {
    toggleTheme();
  };
  useEffect(() => {
    const fetchData = async () => {
      try {
        const userId = usersService.getUser()._id;
        const response = await usersService.getUserById(userId);
        setUserObj(response.data);
      } catch (error) {}
    };

    fetchData();
  }, [user]);

  return (
    <nav className="navbar  navbar-expand-md bg-info">
      <div className="container-fluid">
        <NavLink to={"/final-project-react"} className="navbar-brand">
          <span> Visit{<i className="bi bi-person"></i>}Cards</span>
        </NavLink>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav flex-grow-1">
            <li className="nav-item">
              <NavLink
                to={"/about"}
                className="nav-link"
                aria-current="page"
                href="..."
              >
                About
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                to={"/contact-us"}
                className="nav-link"
                aria-current="page"
                href="..."
              >
                Contact Us
              </NavLink>
            </li>

            {user ? (
              <li className="nav-item">
                <NavLink
                  to={"/fav-cards"}
                  className="nav-link"
                  aria-current="page"
                  href="..."
                >
                  Fav Cards
                </NavLink>
              </li>
            ) : (
              ""
            )}
            {user && user.isBusiness ? (
              <li className="nav-item">
                <NavLink
                  to={"/my-cards"}
                  className="nav-link"
                  aria-current="page"
                  href="..."
                >
                  My-cards
                </NavLink>
              </li>
            ) : (
              ""
            )}
            {user && user.isAdmin ? (
              <li className="nav-item">
                <NavLink
                  to={"/admin-crm"}
                  className="nav-link"
                  aria-current="page"
                  href="..."
                >
                  CRM
                </NavLink>
              </li>
            ) : (
              ""
            )}
          </ul>
          <SearchBar />
          <div className="checkbox-wrapper-54 mx-2">
            <label className="switch">
              <input type="checkbox" onClick={handleSubmit} />
              <span className="slider"></span>
            </label>
          </div>
          <ul className="navbar-nav me-4">
            {user ? (
              <>
                <li className="nav-item dropdown">
                  <a
                    className="nav-link dropdown-toggle"
                    href="..."
                    id="userDropdown"
                    role="button"
                    data-bs-toggle="dropdown"
                    aria-haspopup="true"
                    aria-expanded="false"
                  >
                    <img
                      src={userObj.image && userObj.image.url}
                      alt="User Avatar"
                      className="user-avatar"
                    />
                  </a>
                  <div className="dropdown-menu" aria-labelledby="userDropdown">
                    <ul className="list-unstyled p-2">
                      <li>{userObj.name && userObj.name.first}</li>
                      <li>{userObj.email}</li>
                    </ul>
                    <NavLink to={"/profile"} className="dropdown-item">
                      Edit Profile
                    </NavLink>
                    <div className="dropdown-divider"></div>
                  </div>
                </li>
                <li className="nav-item">
                  <NavLink to={"/sign-out"} className="nav-link" href="...">
                    Sign Out
                  </NavLink>
                </li>
              </>
            ) : (
              <>
                <li className="nav-item">
                  <NavLink to={"/sign-in"} className="nav-link" href="...">
                    Sign In
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink to={"/sign-up"} className="nav-link" href="...">
                    Sign Up
                  </NavLink>
                </li>
              </>
            )}
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
