import httpService from "./httpService";
import { jwtDecode } from "jwt-decode";
const TOKEN_KEY = "token";

refreshTokenHeader();


export function refreshTokenHeader() {
  httpService.setCommonHeader("x-auth-token", getJWT());
}

export async function register(user) {
  const response = await httpService.post("/users", user);
  return response;
}

export async function updateUser(id,update){
  const response = await httpService.put(`/users/${id}`, update)
  window.location.reload()
  return response
}

export async function deleteUser(id){
  const response = await httpService.delete(`/users/${id}`)
  return response
}

export async function patchUserBusinessStatus(id){
  const res = await httpService.patch(`/users/${id}`)
  return res
}

export async function getAllUsers() {
  const response = await httpService.get("/users");
  return response
}

export async function getUserById(id){
return await httpService.get(`/users/${id}`)
}

export async function login(credentials) {
  const response = await httpService.post("/users/login", credentials);
  localStorage.setItem(TOKEN_KEY, response.data);
  refreshTokenHeader();

  return response;
}

export function logout() {
  localStorage.removeItem(TOKEN_KEY);
  refreshTokenHeader();
}

export function getJWT() {
  return localStorage.getItem(TOKEN_KEY);
}

export function getUser() { //NAO COLOCAR COMO ASYNC DE JEITO NENHUM
  try {
    const token = getJWT();
    return jwtDecode(token);
  } catch {
    return null;
  }
}

const usersService = {
  register,
  login,
  logout,
  updateUser,
  deleteUser,
  patchUserBusinessStatus,
  getAllUsers,
  getUserById,
  getUser,
  getJWT,
};

export default usersService;
