import httpService from "./httpService";

export async function getAllCards() {
  const response = await httpService.get("/cards")
  return response.data
}
export function getAllMyCards() {
  return httpService.get("/cards/my-cards");
}

export async function createCard(newCardObj) {
  return await httpService.post("/cards", newCardObj);
}

export async function likeCard(cardId){
  const response = await httpService.patch(`/cards/${cardId}`)
return response.data
}
export async function deleteCard(cardId) {
   await httpService.delete(`/cards/${cardId}`);
}

export async function getCardById(cardId){
  const res = await httpService.get(`/cards/${cardId}`)
  return res.data
}

export async function updateCard(cardId, updatedCard){
  return await httpService.put(`/cards/${cardId}`, updatedCard)

}

const cardsService = {
  getAllCards,
  getAllMyCards,
  createCard,
  likeCard,
  deleteCard,
  getCardById,
  updateCard,
};

export default cardsService;
