import { useFormik } from "formik";
import { useState } from "react";
import { Navigate, useNavigate } from "react-router-dom";
import Joi from "joi";

import Input from "./commons/input";
import { useAuth } from "../context/user.context";
import { validateFormikUsingJoi } from "../utilis/validateFormikUsingJoi";

const SignIn = ({ titlePage,redirect }) => {
  const [serverError, setServerError] = useState("");
  const { user, login } = useAuth();
  const navigate = useNavigate();

  const formik = useFormik({
    validateOnMount: true,
    initialValues: {
      email: "",
      password: "",
    },
    validate: validateFormikUsingJoi({
      email: Joi.string()
        .min(5)
        .max(255)
        .email({ tlds: { allow: false } })
        .required(),
      password: Joi.string().min(8).max(20).required(),
    }),

    async onSubmit(values) {
      try {
        await login(values);
        navigate(redirect);
      } catch (error) {
        setServerError(error.response.data);
      }
    },
  });
  if (user) {
    return <Navigate to={"/"} />;
  }
  
  return (
    <>
      {serverError && <div className="alert alert-danger mt-3">{serverError}</div>}
      <h1 className="text-center mt-3">{titlePage}</h1>
      <form
        onSubmit={formik.handleSubmit}
        className=" container text-center d-flex justify-content-center flex-column gap-2 mt-5 w-75"
      >
        <div>
          <Input
            {...formik.getFieldProps("email")}
            label={"email"}
            title={"Email"}
            error={formik.touched.email && formik.errors.email}
            required
          />
          <Input
            {...formik.getFieldProps("password")}
            label={"password"}
            title={"Password"}
            error={formik.touched.password && formik.errors.password}
            type="password"
            required
          />
        </div>

        <button
          disabled={!formik.isValid}
          type="submit"
          className="btn btn-info my-3"
        >
          Submit
        </button>
      </form>
    </>
  );
};

export default SignIn;
