import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { useFormik } from "formik";
import Joi from "joi";

import cardsService from "../services/cardsService";
import { validateFormikUsingJoi } from "../utilis/validateFormikUsingJoi";
import Input from "./commons/input";
import Alert from "./commons/alerts";

const EditCard = ({ titlePage, redirect }) => {
  const [serverError, setServerError] = useState("");
  const [showSuccessAlert, setShowSuccessAlert] = useState(false);
  const [card, setCard] = useState({});

  const navigate = useNavigate();
  const { id } = useParams();

  useEffect(() => {
    const edit = async (id) => {
      const res = await cardsService.getCardById(id);
      setCard(res);
      
    };
    edit(id);
  }, [id]);

  useEffect(() => {
    if(!card){
      return null
    }
    formik.setValues({
      title: card.title,
      subtitle: card.subtitle ,
      description: card.description ,
      phone: card.phone ,
      email: card.email ,
      web: card.web ,
      url: card.image?.url ,
      alt: card.image?.alt ,
      state: card.address?.state ,
      country: card.address?.country ,
      city: card.address?.city ,
      street: card.address?.street ,
      houseNumber: card.address?.houseNumber ,
      zip: card.address?.zip ,
    })
  },[card])

  
  const formik = useFormik({
    validateOnMount: true,
    initialValues: {
      title: "",
      subtitle: "",
      description: "",
      phone: "",
      email: "",
      web: "",
      url: "",
      alt: "",
      state: "",
      country: "",
      city: "",
      street: "",
      houseNumber: "",
      zip: "",
    },

    validate: validateFormikUsingJoi({
      title: Joi.string().min(2).max(256).required(),
      subtitle: Joi.string().min(2).max(256).required(),
      description: Joi.string().min(2).max(1024).required(),
      phone: Joi.string().min(9).max(11).required(),
      email: Joi.string().min(5).required(),
      web: Joi.string().min(14).allow(""),
      url: Joi.string().min(14).allow(""),
      alt: Joi.string().min(2).max(256).allow(""),
      state: Joi.string().allow(""),
      country: Joi.string().required(),
      city: Joi.string().required(),
      street: Joi.string().required(),
      houseNumber: Joi.number().min(1).required(),
      zip: Joi.number().allow(""),
    }),
    async onSubmit(values) {
      try {
        const {
          title,subtitle,description,phone,email,web,url,alt,state,country,city,street,houseNumber,zip,
        } = values;

        const updateCardObj = {
          title,
          subtitle,
          description,
          phone,
          email,
          web,
          image: {url,alt,},
          address: {country,state,city,street,houseNumber,zip,},
        };

        await cardsService.updateCard(id, updateCardObj);
        setShowSuccessAlert(true);
        setTimeout(() => {
          navigate(redirect);
        }, 1 * 1000);
      } catch (error) {
        setServerError(error.response.data);
      }
    },
  });

  return (
    <>
      <h1 className="text-center mt-3">{titlePage}</h1>
      <form onSubmit={formik.handleSubmit} className="form my-4 row">
        {serverError && <div className="alert alert-danger">{serverError}</div>}
        <div className="d-flex gap-2 mb-2">
          <div className="col-md-4">
            <Input
              {...formik.getFieldProps("title")}
              title={"Title"}
              name={"title"}
              placeholder={card.title}
              error={formik.touched.title && formik.errors.title}
              required
            />
          </div>
          <div className="col-md-4 ">
            <Input
              {...formik.getFieldProps("subtitle")}
              title={"Subtitle"}
              name={"subtitle"}
              placeholder={card.subtitle}
              error={formik.touched.subtitle && formik.errors.subtitle}
              required
            />
          </div>
          <div className="col-md-4">
            <Input
              {...formik.getFieldProps("description")}
              title={"Description"}
              name={"description"}
              placeholder={card.description}
              error={formik.touched.description && formik.errors.description}
              required
            />
          </div>
        </div>

        <div className="d-flex gap-2 mb-2">
          <div className="col-md-6">
            <Input
              {...formik.getFieldProps("phone")}
              title={"Phone"}
              name={"phone"}
              placeholder={card.phone}
              error={formik.touched.phone && formik.errors.phone}
              required
            />
          </div>
          <div className="col-md-6">
            <Input
              {...formik.getFieldProps("email")}
              title={"Email"}
              name={"email"}
              placeholder={card.email}
              error={formik.touched.email && formik.errors.email}
              required
            />
          </div>
        </div>

        <div className="d-flex gap-2 mb-2">
          <div className="col-md-4">
            <Input
              {...formik.getFieldProps("web")}
              title={"Web"}
              name={"web"}
              placeholder={card.web}
              error={formik.touched.web && formik.errors.web}
            />
          </div>

          <div className="col-md-4">
            <Input
              {...formik.getFieldProps("url")}
              title={"Url"}
              name={"url"}
              placeholder={card.image?.url}
              error={formik.touched.url && formik.errors.url}
            />
          </div>
          <div className="col-md-4">
            <Input
              {...formik.getFieldProps("alt")}
              title={"Alternative Text"}
              name={"alt"}
              placeholder={card.image?.alt}
              error={formik.touched.alt && formik.errors.alt}
            />
          </div>
        </div>

        <div className="d-flex gap-2 mb-2">
          <div className="col-md-4">
            <Input
              {...formik.getFieldProps("state")}
              title={"State"}
              name={"state"}
              placeholder={card.address?.state}
              error={formik.touched.state && formik.errors.state}
            />
          </div>
          <div className="col-md-4">
            <Input
              {...formik.getFieldProps("country")}
              title={"Country"}
              name={"country"}
              placeholder={card.address?.country}
              error={formik.touched.country && formik.errors.country}
              required
            />
          </div>
          <div className="col-md-4">
            <Input
              {...formik.getFieldProps("city")}
              title={"City"}
              name={"city"}
              placeholder={card.address?.city}
              error={formik.touched.city && formik.errors.city}
              required
            />
          </div>
        </div>
        <div className="d-flex gap-2">
          <div className="col-md-4">
            <Input
              {...formik.getFieldProps("street")}
              title={"Street"}
              name={"street"}
              placeholder={card.address?.street}
              error={formik.touched.street && formik.errors.street}
              required
            />
          </div>
          <div className="col-md-4">
            <Input
              {...formik.getFieldProps("houseNumber")}
              title={"House Number"}
              name={"houseNumber"}
              placeholder={card.address?.houseNumber}
              error={formik.touched.houseNumber && formik.errors.houseNumber}
              required
            />
          </div>
          <div className="col-md-4">
            <Input
              {...formik.getFieldProps("zip")}
              title={"Zip-code"}
              name={"zip"}
              placeholder={card.address?.zip}
              error={formik.touched.zip && formik.errors.zip}
            />
          </div>
        </div>
        <div className="my-3">
          {showSuccessAlert && (
            <Alert message="Card edit successfully!" type="success" />
          )}
        </div>

        <button type="submit" className="btn btn-info my-3">
          Edit card
        </button>
      </form>
    </>
  );
};

export default EditCard;
