import { Route, Routes } from "react-router-dom";
import "./App.css";

import Navbar from "./components/navbarTop";
import SignIn from "./components/signIn";
import SignUp from "./components/signUp";
import About from "./components/about";
import MyCards from "./components/my-cards";
import SignOut from "./components/signOut";
import HomePage from "./components/homePage";
import ProtectedRouter from "./components/commons/protectedRouter";
import FavCards from "./components/cardsFav";
import CreateCard from "./components/cardCreate";
import CardDelete from "./components/cardDelete";
import NavbarBottom from "./components/navbarBottom";
import EditCard from "./components/cardEdit";
import ProfileEdit from "./components/profileEdit";
import { useSearch } from "./context/search.context";
import { useTheme } from "./context/theme.context";
import ContactUs from "./components/contactus";
import SearchResult from "./components/searchResult";
import AdminPage from "./components/adminCrm";
import AdminEdit from "./components/adminEdit";
import PageNotFound from "./components/404NotFound";
import BusinessPage from "./components/businessPage";
import NavigatePage from "./components/navigatePage";

function App() {
  const { theme } = useTheme();
  const { cardToShow } = useSearch();
  return (
    <div
      className={`App d-flex flex-column min-vh-100 ${
        theme === "dark" ? "theme-dark" : ""
      }`}
    >
      <header>
        <Navbar />
      </header>
      <main className="flex-fill container">
        <Routes>
          <Route path="*" element={<PageNotFound />} />
         {/* <Route path="/final-project-react" element={<NavigatePage />} />*/}
          <Route path="/final-project-react" element={<HomePage />} />
          <Route path="/business-page/:id" element={<BusinessPage />} />
          <Route
            path="/search-results"
            element={cardToShow && <SearchResult card={cardToShow} />}
          />
          <Route path="/contact-us" element={<ContactUs />} />
          <Route
            path="/about"
            element={
              <About
                pageTitlte={"About Us"}
                pageDescription={
                  "We are a very serious company that make your visit card with love and imagination, contactUs"
                }
              />
            }
          />
          <Route
            path="/sign-in"
            element={<SignIn titlePage={"Sign In"} redirect={"/final-project-react"} />}
          />
          <Route
            path="/sign-up"
            element={<SignUp titlePage={"Sign Up"} redirect={"/final-project-react"} />}
          />
          <Route
            path="/my-cards"
            element={
              <ProtectedRouter onlyBiz>
                <MyCards titlePage={"My Cards"} />
              </ProtectedRouter>
            }
          />
          <Route
            path="/createCard"
            element={
              <ProtectedRouter onlyBiz>
                <CreateCard
                  titlePage={"Create a New Card"}
                  redirect={"/my-cards"}
                />
              </ProtectedRouter>
            }
          />
          <Route
            path="/admin-crm"
            element={
              <ProtectedRouter onlyAdmin>
                <AdminPage
                  titlePage={"Admin CRM"}
                />
              </ProtectedRouter>
            }
          />
          <Route
            path="/admin-edit/:id"
            element={
              <ProtectedRouter onlyAdmin>
                <AdminEdit
                  titlePage={"Edit User for ADMIN"}
                />
              </ProtectedRouter>
            }
          />
          <Route
            path="/profile"
            element={
              <ProtectedRouter>
                <ProfileEdit titlePage={"Edit Profile"} redirect={"/final-project-react"} />
              </ProtectedRouter>
            }
          />
          <Route
            path="/cards/:id"
            element={
              <ProtectedRouter onlyBiz>
                <CardDelete redirect={"/my-cards"} />
              </ProtectedRouter>
            }
          />
          <Route
            path="/cards/edit/:id"
            element={
              <ProtectedRouter onlyBiz>
                <EditCard titlePage={"Edit Card"} redirect={"/my-cards"} />
              </ProtectedRouter>
            }
          />

          <Route
            path="/fav-cards"
            element={
              <ProtectedRouter>
                <FavCards titlePage={"Favorite Cards"} />
              </ProtectedRouter>
            }
          />

          <Route path="/sign-out" element={<SignOut />} />
        </Routes>
        <div className="border-top mb-4  py-2 text-center">
          <span>
            Visit<i className="bi bi-person"></i>Cards{" "}
          </span>
          <span className="mx-2">&copy;</span>
          <span>{new Date().getFullYear()}</span>
        </div>
      </main>
      <footer className="mt-5">
        <NavbarBottom />
      </footer>
    </div>
  );
}

export default App;
