import { useSearch } from "../context/search.context";
import Card from "./card";

const SearchResult = ({ card }) => {
  const { cardToShow, search } = useSearch();
  if (!card) {
    return null;
  }
  if (cardToShow.length === 0) {
    return <p className="text-center display-5 my-5">Sorry, but we couldn't find a card with the title: "{search}" </p>;
  }
  return (
    <div className="d-flex justify-content-center gap-2 flex-wrap">
      {cardToShow && cardToShow.map((item) => <Card card={item} />)}
    </div>
  );
};

export default SearchResult;
