import { useEffect, useState } from "react";
import cardsService from "../services/cardsService";
import { useParams } from "react-router-dom";
/*`https://www.google.com/maps/embed/v1/place?key=AIzaSyDW-ljK5j0t6IAULYKj3rIb7atKQfagnpY
&q=Space+Needle,Seattle+WA`*/
const BusinessPage = () => {
  const [business, setBusiness] = useState({});
  const { id } = useParams();

  useEffect(() => {
    async function fetchData(id) {
      const res = await cardsService.getCardById(id);
      setBusiness(res);
    }
    fetchData(id);
  }, []);

  const addressQuery = encodeURIComponent(
    `${business.address?.houseNumber} ${business.address?.street}, ${business.address?.city}, ${business.address?.state}, ${business.address?.country}`

  );

  const src = `https://www.google.com/maps/embed/v1/place?key=AIzaSyDW-ljK5j0t6IAULYKj3rIb7atKQfagnpY&q=${addressQuery}`;

  return (
    <div className="my-3">
      <h1 className="text-center">{business.title}</h1>
      <h3 className="text-center">{business.subtitle}</h3>
      <p className="text-center">
        <b>Business Description:</b> {business.description}
      </p>
      <div className="d-flex justify-content-center">
        <img style={{ width: "250px" }} src={business.image?.url} alt="" />
        <ul className="flex-column ms-3  list-unstyled">
          <div>
            <h4>Business Address</h4>
          </div>
          <li>
            <b>Country: </b>
            {business.address?.country}
          </li>
          <li>
            <b>state: </b> {business.address?.state || "not provide"}
          </li>
          <li>
            <b>city: </b> {business.address?.city}
          </li>
          <li>
            <b>street: </b> {business.address?.street}
          </li>
          <li>
            <b>houseNumber: </b> {business.address?.houseNumber}
          </li>
          <li>
            <b>zip: </b> {business.address?.zip || "not provide"}
          </li>
        </ul>
      </div> 
        <div className="d-flex justify-content-center mt-4">
            <iframe
            className="justify-content-center"
              width="600"
              title="maps"
              height="450"
              loading="lazy"
              allowFullScreen
              referrerPolicy="no-referrer-when-downgrade"
              src={src}
            ></iframe>
        </div>
    </div>
  );
};

export default BusinessPage;
